import React from 'react';
import Typewriter from "typewriter-effect";
import styles from "./Typewriter.module.css";

const Type: React.FC = () => {
    return (
        <Typewriter
            options={{
                wrapperClassName: styles.typewriter__wrapper,
                cursorClassName: styles.typewriter__cursor,
                strings: [
                    `Backend Developer`,
                    `Agile Aficionado`,
                    `Maven maven`,
                    `Rust rooter`
                ],
                autoStart: true,
                loop: true,
                deleteSpeed: 50,
            }}
            onInit={typewriter => typewriter.start()}/>
    );
};

export default Type;
